*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
	font-family: $font-family-stem;
	font-size: 0.8333vw;
	@media all and (max-width: 1280px){ 
		font-size: 16px;
	}
}

body {
	background: $color-alabaster-light;
}

.page-list {
	margin-top: 2.5rem;
	margin-bottom: 2.5rem;
	font-size: 1.125rem;
	background: $color-white;
	thead {
		td {
			font-weight: 700;
			text-transform: uppercase;
		}
	}
	li {
		margin-bottom: 1.25rem;
		&:last-of-type {
			margin-bottom: 0;
		}
	}
	a {
		color: $color-curious-blue;
		text-decoration: none;
	}
	td {
		padding: 1.25rem;
		vertical-align: top;
		border: 1px solid $color-franch-gray;
		border-bottom: none;
		// &:first-of-type {
		// 	text-transform: uppercase;
		// 	font-weight: 700;
		// }
	}
	tr {
		&.question {
			background: $color-aquamarine;
		}

		&.not-ready {
			background: $color-geyser;
		}
		
		&:last-of-type {
			border-bottom: 1px solid $color-franch-gray;
		}
	}
}
// input
.input-unify {
	display: block;
	width: 100%;
	height: 3.75rem;
	margin-bottom: 1.875rem;
	padding: 1.5rem 0 0.625rem 1.25rem;
	box-sizing: border-box;
	border-radius: 0.3125rem;
	border: 1px solid $color-mercury;
	color: $color-mine-shaft;
	font-size: 1.125rem;
	line-height: 1.5rem;
	background: $color-alabaster-light;
	&::placeholder {
		color: $color-silver;
		font-size: 1.125rem;
	}
}

.input-file-unify {
	display: none;
}

.textarea-unify {
	display: block;
	width: 100%;
	height: auto;
	min-height: 16.25rem;
	margin-bottom: 1.875rem;
	padding: 1.25rem;
	box-sizing: border-box;
	border-radius: 0.3125rem;
	border: 1px solid $color-mercury;
	color: $color-mine-shaft;
	font-size: 1.125rem;
	line-height: 1.875rem;
	background: $color-alabaster-light;
	&::placeholder {
		color: $color-silver;
		font-size: 1.125rem;
	}
}

.label-unify {
	display: inline-block;
	margin-bottom: 0.875rem;
	padding-left: 0.625rem;
	color: $color-mine-shaft;
	font-size: 0.875rem;
}

.label-file-unify {
	display: inline-block;
	font-size: 0;
}

.animation-unify {
	transition: all 0.33s ease;
}

// header 
.header {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	z-index: 9;
	box-shadow: 0 0 1.25rem rgba(0, 0, 0, 0.25);
	background-color: $color-white;
	&__container {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 6.25rem;
	}

	&__logo {
		width: 9.8125rem;
		height: auto;
		margin-right: 1.875rem;
		margin-left: 1.875rem;
	}

	&__info {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-right: 5.625rem;
	}

	&__login {
		margin-right: 2.5rem;
	}

	&__name {
		font-size: 1.5rem;
		font-weight: 700;
		color: $color-mine-shaft;
	}

	&__swither {
		font-size: 0.875rem;
		color: $color-curious-blue;
		text-decoration: underline;
	}

	&__notice {
		position: relative;
		margin-right: 0.3125rem;
	}

	&__notice-quantity {
		position: absolute;
		top: -0.5625rem;
		right: -0.3125rem;
		width: 1.125rem;
		height: 1.125rem;
		background: $color-sunset-orange;
		font-size: 0.75rem;
		color: $color-white;
		text-align: center;
		line-height: 1.125rem;
		border-radius: 50%;
	}
	
	@media all and (max-width: 1024px){ 
		&__info {
			margin-right: 1.875rem;
		}
	}

	@media all and (max-width: 768px){ 
		&__container {
			height: 3.75rem;
		}
		
		&__logo {
			width: 6.25rem;
			margin-left: 0.9375rem;
		}

		&__name {
			font-size: 1.125rem;
		}

		&__login {
			margin-right: 0.9375rem;
		}

		&__info {
			margin-right: 0.9375rem;
		}
	}
}
// aside
.sidebar {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	z-index: 8;
	width: 15.625rem;
	padding-top: 6.25rem;
	box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.25);
	background: linear-gradient(to top, #243873, #1b96d1);
	
	&__menu {
		margin-top: 3.75rem;
		margin-bottom: 0;
		padding-left: 0;
		list-style: none;
	}

	&__link {
		display: flex;
		align-items: center;
		height: 5rem;
		padding-left: 1.875rem;
		box-sizing: border-box;
		color: $color-periwinkle;
		text-decoration: none;
		cursor: pointer;
		
		.dashboard {
			background: url("../images/dashboard.svg") center no-repeat;	
			background-size: contain;		
		}
		
		.sponsor {
			background: url("../images/sponsor.svg") center no-repeat;			
			background-size: contain;
		}

		.handshake {
			background: url("../images/handshake.svg") center no-repeat;			
			background-size: contain;
		}

		.network {
			background: url("../images/network.svg") center no-repeat;			
			background-size: contain;
		}

		.dashboard {
			background: url("../images/dashboard.svg") center no-repeat;			
			background-size: contain;
		}

		&:hover,
		&.active {
			color: $color-white;
			background: $color-curious-blue;
			
			.dashboard {
				background: url("../images/dashboard_active.svg") center no-repeat;	
				background-size: contain;		
			}
			
			.sponsor {
				background: url("../images/sponsor_active.svg") center no-repeat;	
				background-size: contain;		
			}
			
			.handshake {
				background: url("../images/handshake_active.svg") center no-repeat;	
				background-size: contain;		
			}
			
			.network {
				background: url("../images/network_active.svg") center no-repeat;	
				background-size: contain;		
			}
		}
	}
	
	&__image {
		width: 2rem;
		height: 2rem;
		margin-right: 0.9375rem;
	}
	
	@media all and (max-width: 1024px){
		width: 3.75rem;
		&__text {
			display: none;
		}

		&__link {
			padding-left: 0.875rem;
		}

		&__menu {
			margin-top: 0;
		}
	}
	@media all and (max-width: 768px){
		padding-top: 3.75rem;

		&__link {
			height: 4.0625rem;
		}
	}
}
.dashboard {
	position: relative;
	min-height: calc(100vh - 3.75rem);
	&__container {
		width: 120rem;
		max-width: 100%;
		padding-top: 6.25rem;
		padding-left: 19.0625rem;
		padding-right: 2.1875rem;
		box-sizing: border-box;
		&_without-sidebar {
			padding-left: 2.1875rem;
		}

		&_footer-padding {
			padding-bottom: 3.125rem;
		}
	}	
	
	&__header {
		width: 100%;
		margin-top: 2.5rem;
		margin-bottom: 2.5rem;
		padding-right: 0.9375rem;
		padding-left: 0.9375rem;	
	}
	
	&__statistic,
	&__title {
		margin-top: 0;
		margin-bottom: 0;
		color: $color-mine-shaft;
		font-size: 2.25rem;
		font-weight: 700;
	}
	
	&__title {
		display: inline-block;
		margin-right: 1.875rem;
	}

	&__edit {
		display: inline-block;
		color: $color-silver;
		font-size: 1.125rem;
		font-weight: 700;
		text-decoration: none;

		&::before {
			display: inline-block;
			width: 0.5rem;
			height: 0.5rem;
			margin-right: 0.25rem;
			content: '';
			background: url("../images/edit.png") center no-repeat;	
			background-size: contain;	
		}
	}

	&__item {
		min-height: 12.5rem;
		margin-right: 0.9375rem;
		margin-bottom: 1.875rem;
		margin-left: 0.9375rem;
		box-shadow: 0.25rem 0.25rem 2.5rem rgba(29, 149, 209, 0.1);
		border-radius: 0.3125rem;
		background-color: $color-white;
		box-sizing: border-box;
	}

	&__section-header {
		width: calc(100% - 1.875rem);
		margin: 2.5rem 0.9375rem;
		color: $color-mine-shaft;
		font-size: 2.25rem;
		font-weight: 700;
	}
	
	@media all and (max-width: 1024px){
		&__container {
			padding-left: 3.75rem;
			padding-right: 0;
			&_without-sidebar {
				padding-left: 0;
			}
		}
	}

	@media all and (max-width: 768px){
		&__container {
			padding-top: 3.75rem;
		}		
	}
	@media all and (max-width: 568px){
		&__title {
			font-size: 2rem;
		}
	}
}
ul,
h3,
h2,
p {
	margin-top: 0;
	margin-bottom: 0;
}
ul {
	padding-left: 0;
	list-style: none;
}

.dashboard__row {
	display: flex;
	flex-wrap: wrap;
	width: 98.75rem;
	&_first {
		flex-wrap: nowrap;
		margin-bottom: 1.875rem;
	}

	&_second {
		flex-direction: column;
	}

	&_platform {
		padding-bottom: 3.125rem;
	}

	&_financials {
		flex-wrap: wrap;
		width: 98.75rem;
		// width: 100%;
		// max-width: 100%;
		// max-width: 98.75rem;
		// min-width: 98.75rem;
	}

	&_no-header {
		margin-top: 3.75rem;
	}

	&_last {
		padding-bottom: 2.5rem;
	}

	&_listing {
		margin-right: auto;
		margin-left: auto;
	}

	&.justify {
		justify-content: space-between;
	}

	&.center {
		justify-content: center;
	}
	
	@media all and (max-width: 1280px){
		width: 100%;
		flex-wrap: wrap;
		&_first {
			.dashboard__item {
				width: calc(33.3% - 1.875rem);
			}
			
			.dashboard-brand {
				width: 100%;
			}
		}

		&_financials {
			flex-wrap: wrap;
			width: 100%;
		}
	}

	@media all and (max-width: 1024px){
		&_first {
			.dashboard__item {
				padding: 2.5rem 2.5rem 1.5625rem;
			}
			
			.dashboard-brand {
				padding: 0;
			}
		}
	}

	@media all and (max-width: 768px){
		&_first {
			.dashboard__item {
				width: calc(50% - 1.875rem);
				text-align: center;
			}
			.dashboard-brand {
				width: 100%;
				text-align: left;
			}
		}
	}

	@media all and (max-width: 568px){
		&_first {
			.dashboard__item {
				width: 100%;
				padding: 0.9375rem;
			}
			.dashboard-brand {
				padding: 0;
				&__content {
					padding: 0.9375rem;
				}
			}
		}
	}
}

