.dashboard-brand {
	display: flex;
	align-items: stretch;
	width: 47.5rem;
	&__image {
		display: flex;
		justify-content: center;
		align-items: center;
		min-width: 12.5rem;
		background: $color-alabaster;

		img {
			width: 11.25rem;
			max-width: 100%;
		}
	}

	&__content {
		padding: 2.5rem;
	}

	&__header {
		margin-top: 0;
		margin-bottom: 1.25rem;
		color: $color-curious-blue;
		font-size: 2.25rem;
		font-weight: 700;
	}

	&__text {
		margin-top: 0;
		margin-bottom: 0;
		color: $color-mine-shaft;
		font-size: 1.125rem;
	}

	@media all and (max-width: 1280px){
		width: 100%;
	}

	@media all and (max-width: 568px){ 
		flex-wrap: wrap;
		&__image {
			width: 100%;
			padding-top: 0.9375rem;
			padding-bottom: 0.9375rem;
		}
	}
}

.dashboard-foundation,
.dashboard-arrangements,
.dashboard-rating {
	padding: 2.5rem 3.125rem 1.5625rem 3.125rem;
}

.dashboard-rating {
	width: 11.25rem;
	min-width: 11.25rem;
	text-align: center;
	&__header {
		margin-bottom: 1.875rem;
		color: $color-mine-shaft;
		font-size: 1.125rem;
		font-weight: 700;
	}

	&__text {
		color: $color-curious-blue;
		font-size: 3rem;
		font-weight: 700;
	}

	@media all and (max-width: 1280px){
		min-width: 0;
		width: 12.5rem;
	}

	@media all and (max-width: 568px){
		width: calc(100% - 1.875rem);
	}
	
}

.dashboard-arrangements {
	width: 16.25rem;
	min-width: 16.25rem;
	&__header {
		margin-bottom: 0.9375rem;
		color: $color-franch-gray;
		font-size: 3rem;
		font-weight: 700;
	}

	&__text {
		color: $color-gray;
		font-size: 1.125rem;
	}
	
	@media all and (max-width: 1280px){
		min-width: 0;
		width: calc(50% - 1.875rem);
	}
	
	@media all and (max-width: 1024px){ 
		padding: 2.5rem;
	}

	@media all and (max-width: 768px){ 
		text-align: center;
	}

	@media all and (max-width: 568px){ 
		width: 100%;
		padding: 0.9375rem;
	}
}

.dashboard-foundation {
	width: 16.25rem;
	min-width: 16.25rem;
	&__header {
		margin-bottom: 0.9375rem;
		color: $color-curious-blue;
		font-size: 3rem;
		font-weight: 700;
	}

	&__text {
		color: $color-gray;
		font-size: 1.125rem;
	}

	@media all and (max-width: 1280px){
		min-width: 0;
		width: calc(50% - 1.875rem);
	}

	@media all and (max-width: 1024px){ 
		padding: 2.5rem;
	}
	
	@media all and (max-width: 768px){ 
		text-align: center;
	}

	@media all and (max-width: 568px){ 
