		padding: 0.9375rem;
	}
}

.dashboard-financials {
	position: relative;
	width: 47.5rem;
	min-width: 47.5rem;
	padding: 2.5rem;
	&__header {
		margin-bottom: 2.5rem;
		padding-right: 1.875rem;
		padding-left: 1.875rem;
		color: $color-mine-shaft;
		font-size: 1.5rem;
		font-weight: 700;
	} 
	
	&__chart {
		margin-bottom: 2.5rem;
		img {
			max-width: 100%;
		}
	}
	
	&__values {
		display: flex;
		justify-content: space-between;
		padding-right: 1.875rem;
		padding-left: 1.875rem;
	}
	
	&__item {
		width: 33.3%;
		text-align: center;
	}

	&__spend {
		margin-bottom: 0.3125rem;
		color: $color-mine-shaft;
		font-size: 0.875rem;
		font-weight: 300;
	}

	&__value {
		margin-bottom: 0.625rem;
		color: $color-mine-shaft;
		font-size: 0.875rem;
		font-weight: 700;
	}

	&__spons {
		color: $color-curious-blue;
		font-size: 1.125rem;
		font-weight: 700;
	}

	&__chart-point {
		position: absolute;
		//   
		top: 143px;
		left: 288px;
		display: none;
		width: 0.5rem;
		height: 0.5rem;
		border-radius: 50%;
		background: $color-curious-blue;
		&.hidden {
			display: none;
		}
		
		&.checked {
			display: block;
		}	
	}
	&__chart-description {
		position: absolute;
		top: 3.125rem;
		//   
		left: 288px;
		display: none;
		min-width: 9.375rem;
		height: 5rem;
		padding: 0.9375rem;
		// padding: 0.9375rem 0.9375rem 0.9375rem  1.25rem;
		box-shadow: 0.25rem 0.25rem 1.25rem rgba(29, 149, 209, 0.1);
		border-radius: 0.3125rem;
		background-color: $color-white;
		&.hidden {
			display: none;
		}
		
		&.checked {
			display: block;
		}
	}

	&__chart-value-1 {
		margin-right: 0.625rem;
		color: $color-curious-blue;
		font-size: 1.5rem;
	}
	
	&__chart-value-2 {
		color: $color-malachite;
		font-size: 0.75rem;
		&.down {
			color: $color-sunset-orange;
		}
	}
	
	&__chart-value-3 {
		color: $color-curious-blue;
		font-size: 0.875rem;
		font-weight: 700;
	}

	@media all and (max-width: 1280px){
		flex-grow: 2;
		width: 100%;
		min-width: 0;
		&__chart {
			text-align: center;
		}
	}

	@media all and (max-width: 768px){ 
		&__values {
			padding-right: 0;
			padding-left: 0;
		}
	}

	@media all and (max-width: 568px){ 
		padding: 0.9375rem;
		&__values {
			flex-wrap: wrap;
		}

		&__item {
			width: 50%;
			margin-bottom: 1.875rem;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	@media all and (max-width: 425px){ 
		&__item {
			width: 100%;
		}
	}
}

.dashboard-sponsees {
	align-self: flex-start;
	width: 47.5rem;
	padding: 2.5rem 4.375rem;
	&__header {
		margin-bottom: 1.25rem;
		color: $color-mine-shaft;
		font-size: 1.5rem;
		font-weight: 700;
	}
	
	&__list {
		margin-top: 0;
		margin-bottom: 0;
		padding-left: 0;
		font-size: 0;
		list-style: none;
	}

	&__item {
		display: inline-block;
		width: 50%;
		min-width: 12.5rem;
		margin-bottom: 0.9375rem;
		color: $color-gray;
		font-size: 0.875rem;		
		&::before {
			display: inline-block;
			width: 0.875rem;
			height: 0.625rem;
			margin-right: 0.625rem;
			content: '';
			background: url("../images/check.svg") center no-repeat;
			background-size: contain;			
		}
	}

	@media all and (max-width: 1280px){
		width: 100%;
		padding: 2.5rem;
		&__item {
			width: 25%;
		}
	}

	@media all and (max-width: 568px){
		padding: 0.9375rem;
		&__item {
			display: block;
			width: 100%;
			min-width: 0;
		}
	}
}

.dashboard-textbox {
	width: 47.5rem;
	min-width: 47.5rem;
	// max-width: calc(100% - 1.875rem);
	padding: 2.5rem 3.125rem 3.75rem 4.375rem;
	&__header {
		margin-bottom: 1.5rem;
		color: $color-mine-shaft;
		font-size: 1.5rem;
		font-weight: 700;
	}

	&__content {
		color: $color-gray;
		font-size: 0.875rem;
		line-height: 1.4375rem;
	}

	@media all and (max-width: 1280px){
		width: 100%;
		min-width: 0;
		padding: 2.5rem;
	}

	@media all and (max-width: 568px){
		padding: 0.9375rem;
	}
}

.dashboard-statistic {
	width: 100%;
	margin-bottom: 2.5rem;
	padding: 2.5rem 4.375rem;
	&__header {
		margin-bottom: 1.75rem;
		color: $color-mine-shaft;
		font-size: 1.5rem;
		font-weight: 700;
	}

	&__container {
		display: flex;
		flex-wrap: wrap;
	}

	&__item {
		display: flex;
		width: 50%;
		margin-bottom: 2.5rem;
	}

	&__chart {
		width: 13.125rem;
