		position: relative;
		display: block;
		width: 1rem;
		height: 1rem;
		margin-top: 1px;
		// margin-left: 0.1875rem;
		&::before {
			position: absolute;
			top: 0;
			left: 0;
			z-index: 7;
			display: block;
			width: 1rem;
			height: 1rem;
			content: '';
			border: 1px solid $color-mercury;
			border-radius: 50%;
			box-sizing: border-box;
			background-color: $color-alabaster;
		}
		&::after {
			position: absolute;
			top: 0.1875rem;
			left: 0.1875rem;
			z-index: 8;
			display: none;
			width: 0.625rem;
			height: 0.625rem;
			content: '';
			border-radius: 50%;
			background-color: $color-curious-blue;
		}
		&:checked::after {
			display: block;
		}
	}
	
	&__text {
		display: block;
		color: $color-silver;
		font-size: 0.625rem;
	}
}

