	@media all and (max-width: 568px){
		&__axis_x .chart__value {
			display: none;
		}

		&__axis_x .chart__value:nth-child(3n-2) {
			display: block;
		}
		
		&__axis_x .chart__value:nth-child(2n) {
			display: none;
		}

		&__axis_x .chart__value:last-child {
			display: block;
		}

	}
}